import React from 'react'

const Categories = ({categories,filterCards}) => {
    return (
        <>
         <h2 className="container-h2">Check out these EPIC Destinations!</h2>
            <div className='btn-container'>
						{categories.map((category,index) =>{
							return <button type="button" className="filter-btn" key={index} onClick={()=> filterCards(category)} >{category}</button>
						})}
            </div>
        </>
    )
}

export default Categories


                 
