import React from 'react'
import Review from './Review';
import './Review.css'

const ReviewSection = () => {
    return (
      <section id="review" className="main-section">
       <div className="container__review">
        <div className="title__review">
          <h2>Our reviews</h2>
          <div className="underline__review"></div>
        </div>
       <Review />
      </div>
    </section>
    )
}

export default ReviewSection;
