const CardPrice = ({items}) => {
        return (
               <div className='cards__service'>
                   <div className="cards__items__service">
                    {items.map((cardItem) => {
                        const {id,airport,title,people,category,price,days,img,revNum,revLet} = cardItem;
                    return(
                          <div key={id}  className="cards__item__service">
                              <div className="cards__item__link__service">
                                <figure className="cards__item__pic-wrap__service" data-category={category} >
                                    <img src={img} alt="Travel Images" className='cards__item__img__service' /> 
                                </figure>
                                <div className="cards__item__info__service">
                                    <h5 className="cards__item__text__service">{title}</h5>
                                    <ul>
                                        <li>£ {price}</li>
                                        <li>{people} poeple</li>
                                        <li>Duration:{days} days</li>
                                        <li><span className="cards__span1">{revNum} </span><span className="cards__span2">{revLet}</span></li>
                                        <li>{airport}</li>
                                    </ul>
                                    <button className="cards__item__button__service">Book It</button>
                                </div>
                              </div>
                           </div> 
                    )
                    })}
                   </div>
         </div>

            )
 }

 export default CardPrice

