
import React,{ useState } from 'react';
import Categories from '../Categories';
import CardPrice from '../CardPrice';
import Header from '../Header'
import items from '../data'

const allCategories = ['all',...new Set(items.map((item) => item.category))];

const Services =() => {
    const [cardItem ,setCardItem] = useState(items);
    const [categories , setCategories] = useState(allCategories);

    const filterCards = (category) => {

			if(category === 'all'){
				setCardItem(items);
				return
			}

    const newCards = items.filter((item) => item.category === category)
    setCardItem(newCards)    
    }
    return(
        <>
        <Header 
            h1="Tops Tours Packages"
              
        />
        <Categories categories={categories} filterCards={filterCards} />
        <CardPrice items={cardItem} />
        </>
    )
}

export default Services;
